import axios from 'axios'
import tools from '@/common/tools.js'

export default {
  name: 'changeusername',
  data: function() {
    return {
      nameTxt: ''
    }
  },
  created() {
    console.log(this.$store.state.userInfo.nickname)
    this.nameTxt = this.$store.state.userInfo.nickname
  },
  methods: {
    setUserName() {
      const url = '/Mobile/User/userinfo.html'
      axios.post(url, {
        nickname: this.nameTxt
      })
        .then((res) => {
          if (res.data.status === 1) {
            tools.showError(res.data.msg)
            this.$store.commit('MODIFY_USERNAME', this.nameTxt)
            this.$router.push({path: 'accountset'})
          } else {
            tools.showError(res.data.msg)
          }
        })
        .catch((error) => {
          console.log(error)
        })
    },
    clearTxt() {
      this.nameTxt = ''
    }
  }
}



